.themeModrnWatrFall.display #marqueecontainer {
    height: 100vh;
    scrollbar-width: none;
}
.themeModrnWatrFall.display #wPosts {
    margin-bottom: 400px;
}
.themeModrnWatrFall .flatCard .post {
    box-shadow: 0 1px 4px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
    background-clip: padding-box;
    border: 0px;
    border-radius: 12px;
    overflow: hidden;
}
.themeModrnWatrFall.display .tb_mwf_post_in {
    cursor: default;
}
.tb_fc_media_wrap {
    position: relative;
    line-height: 0px;
}
.tb_mwf_contant_wrapper {
    padding: 10px 0px 0px 0px;
}
.tb_mwf_author_wrapper {
    width: 100%;
}
.themeModrnWatrFall .theme65 .pixel65 .post {
    padding: 0px !important;
}
.pixel65 .tb_mwf_contant_wrapper{
    position: absolute;
    bottom: 0px;
    width: 100%;
    padding: 8px;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_author_wrapper{
    margin-bottom: 0px;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_author_profile {
    height: 34px;
    width: 34px;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_authorname {
    color: #fff !important;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_post_info > div {
    color: #fff !important;
    font-size: 11px;
}
.pixel65 .tb_mwf_contant_wrapper .tb_mwf_post_info .tb_mwf_seprator {
    background-color: #fff !important;
}
.themeSquare .theme66 .tb_fc_media_wrap video, 
.themeSquare .theme66 .tb_fc_media_wrap iframe,
.themeSquare .theme66 .tb_mwf_post_media_wrapp img{
    /* height: 350px !important; */
    aspect-ratio: 1/1;
}

.themeSquare .theme66 .tb_mwf_contant_wrapper .post-card-outer{
    min-height: 75px;
}
.roundeageTheme .flatCard .post{
  //    border-radius: 10px !important;
    padding: 0px !important;
}
.roundeageTheme .flatCard .post .tb_mwf_contant_wrapper{
    padding: 20px 30px 30px 18px;
}
.roundeageTheme .flatCard .post .tb_mwf_contant_wrapper .tb_mwf_social_{
    float: none;
    border-bottom: 60px solid #cd486b;
    border-left: 60px solid transparent;
    position: absolute;
    left: auto;
    right: 0;
    top: auto;
    bottom: 0;
    margin: 0;
}
.roundeageTheme .flatCard .post .tb_mwf_contant_wrapper .tb_mwf_social_ .tb_mwf_social_ico {
    bottom: -50px;
    top: auto;
    right: 8px;
    position: absolute;
    font-size: 18px;
    color: #fff !important;
}
.roundeageTheme .tb_mwf_social_ico .tb_color_icon__:before {
    color: #fff !important;
}

.roundeageTheme .tb-instagram-default:before{
    color: #fff !important;
}

.roundeageTheme .tb_mwf_authorname{
    font-size: 16px;
}

.roundeageTheme .tb_mwf_author_profile{
    height: 48px;
    width: 48px;
}
